import React, { useEffect, useState } from "react";
import { Map, Marker, GoogleApiWrapper } from "google-maps-react";
import { Hidden } from "@mui/material";

import SearchBox from "../../../../assets/icons/search.svg";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import CrossBox from "../../../../assets/icons/cross-box.svg";

import "./global/map.css";

const MapContainer = ({ google, forBusiness, latlong, setCoordinates }) => {
  const [address, setAddress] = useState(""),
    // [selectedPlace, setSelectedPlace] = useState(""),
    [markerCoordinates, setMarkerCoordinates] = useState({
      lat: 0,
      lng: 0,
    }),
    [mapCenter, setCenter] = useState({
      lat: 0,
      lng: 0,
    });

  const handleChange = (address) => {
    setAddress(address);
  };

  const handleClearInput = () => {
    setAddress("");
    // setSelectedPlace("");
  };

  const handleSelect = (address) => {
    setAddress(address);
    // setSelectedPlace(address);
    geocodeByAddress(address)
      .then((results) => getLatLng(results[0]))
      .then((latLng) => {
        // update center state
        setCenter(latLng);
        setCoordinates(latLng);
      })
      .catch((error) => console.error("Error", error));
  };

  const onMapClicked = (mapProps, map, { latLng: { lat, lng } }) => {
    const newLatLng = { lat: lat(), lng: lng() };
    console.log("marker clicked", mapProps, map, newLatLng);
    setCoordinates(newLatLng);
    setMarkerCoordinates(newLatLng);
  };

  const dragEndHandler = (one, two, three) => {
    const { latLng } = three;
    const lat = latLng.lat();
    const lng = latLng.lng();
    setCenter({
      lat,
      lng,
    });
    setCoordinates({
      lat,
      lng,
    });
  };

  useEffect(() => {
    latlong && setCenter(latlong);
    latlong && setMarkerCoordinates(latlong);
  }, [latlong]);

  return (
    <div id="googleMaps">
      <PlacesAutocomplete
        value={address}
        onChange={handleChange}
        onSelect={handleSelect}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
          <div className="location-search-wrapper">
            <div className="search-box">
              <img
                src={SearchBox}
                className="search-box-icon"
                alt="Search icon"
              />
            </div>
            {address.length > 0 && (
              <div className="cross-box" onClick={handleClearInput}>
                <img
                  src={CrossBox}
                  className="cross-box-icon"
                  alt="Cross icon"
                />
              </div>
            )}
            <input
              {...getInputProps({
                placeholder: "Search Location or Mark Pointer",
                className:
                  suggestions.length > 0
                    ? "location-search-input-active"
                    : "location-search-input",
              })}
            />

            <div className="autocomplete-dropdown-container">
              {loading && <div className="loadingWrapper">Loading...</div>}
              {suggestions.map((suggestion, index) => {
                const className = suggestion.active
                  ? "suggestion-item--active"
                  : "suggestion-item";
                // inline style for demonstration purpose
                const style = suggestion.active
                  ? { backgroundColor: "#fafafa", cursor: "pointer" }
                  : { backgroundColor: "#ffffff", cursor: "pointer" };
                return (
                  <div
                    {...getSuggestionItemProps(suggestion, {
                      className,
                      style,
                    })}
                    key={index}
                  >
                    <span className="main-title">
                      {suggestion.formattedSuggestion.mainText}
                    </span>
                    {suggestion.formattedSuggestion.secondaryText && (
                      <p className="main-subtitle">
                        {suggestion.formattedSuggestion.secondaryText}
                      </p>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </PlacesAutocomplete>
      <div className="map-wrapper">
        <Hidden xsDown implementation="js" only="xs">
          <Map
            containerStyle={{
              position: "relative",
              width: "0px",
              height: "0px",
            }}
            style={{
              position: "relative",
              width: forBusiness ? "578px" : "318px",
              height: forBusiness ? "370px" : "448px",
              borderRadius: "12px",
            }}
            google={google}
            options={{
              fullscreenControl: false,
            }}
            initialCenter={{
              lat: mapCenter.lat,
              lng: mapCenter.lng,
            }}
            center={{
              lat: mapCenter.lat,
              lng: mapCenter.lng,
            }}
            onClick={onMapClicked}
          >
            <Marker
              title="Current Location"
              id={1}
              onDragend={(t, map, coord) => dragEndHandler(t, map, coord)}
              position={{
                lat: markerCoordinates.lat,
                lng: markerCoordinates.lng,
              }}
            />
          </Map>
        </Hidden>
        <Hidden smUp implementation="js">
          <Map
            containerStyle={{
              position: "relative",
              width: "100%",
              height: "100%",
            }}
            style={{
              position: "relative",
              width: "100%",
              height: "370px",
              borderRadius: "12px",
            }}
            google={google}
            options={{
              fullscreenControl: false,
            }}
            initialCenter={{
              lat: mapCenter.lat,
              lng: mapCenter.lng,
            }}
            center={{
              lat: mapCenter.lat,
              lng: mapCenter.lng,
            }}
            onClick={onMapClicked}
          >
            <Marker
              title="Current Location"
              id={1}
              draggable={true}
              onDragend={(t, map, coord) => dragEndHandler(t, map, coord)}
              position={{
                lat: markerCoordinates.lat,
                lng: markerCoordinates.lng,
              }}
            />
          </Map>
        </Hidden>
      </div>
    </div>
  );
};

export default GoogleApiWrapper({
  apiKey: "AIzaSyCJikCEsC6sImWdw2Q97SWsS7145s-TtLo",
})(MapContainer);
