/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import { MenuItem } from "@material-ui/core";
import PhoneInput from "react-phone-input-2";

import { useToastMessage } from "../../../contexts/ToastMessageContext";
import FileAttachment from "../../../Components/UI/FileAttachment";
import { UIButton } from "../../../Components/exports";
import { CustomTextFieldFilled } from "../../../Components/UI/CustomTextField/CustomTextField";
import {
  getBusinessInformation,
  updateGeneralDetail,
} from "../../../api/Business";
import { timeZoneList, industryList } from "../../../utils/GeneralSettingsData";
import countriesList from "../../../data/countries.json";

import Map from "./Map";

import scssClasses from "./formContainer.module.scss";

import useStyles from "./styles/muiStyles";
import { DeleteFile, UploadFile } from "../../../api/ChatFiles";

const GeneralSettingsForm = (props) => {
  const classes = useStyles(),
    { setSuccessMessage, setErrorMessage } = useToastMessage(),
    [newLogo, setNewLogo] = useState(null),
    [oldLogo, setOldLogo] = useState(null),
    [businessData, setBusinessData] = useState({
      business_name: "",
      logo: "",
      industry: "",
      website: "",
      number: "",
      address: "",
      pin_code: "",
      country: "",
      state: "",
      city: "",
      timezoneLabel: "",
      timezoneValue: "",
      latitude: "",
      longitude: "",
    }),
    {
      business_name,
      logo,
      industry,
      website,
      number,
      address,
      pin_code,
      country,
      state,
      city,
      timezoneLabel,
    } = businessData,
    // [info, setInfo] = useState(null),
    [province, setProvince] = useState([]),
    businessId = localStorage.getItem("businessId"),
    inputChangedHandler = (e) => {
      let name = e.target.name;
      let value;
      if (e.target.type === "file") {
        const newLogo = e.target.files[0];
        setNewLogo(newLogo);
      } else {
        value = e.target.value;
        setBusinessData({ ...businessData, [name]: value });
      }
    },
    removeLogo = () => {
      setOldLogo(businessData.logo);
      setNewLogo(null);
      setBusinessData({ ...businessData, logo: null });
    },
    // handleIndustryValue = (e) => {
    // console.log('industry',e.target.value);
    // setBusinessData({ ...businessData, industry: value });
    //},
    HandleInputSelect = async (event) => {
      const value = event.target.value;
      const name = event.target.name;
      setBusinessData({ ...businessData, [name]: value });
      const res = await fetch(`../data/${value}.json`, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const stateList = await res.json();
      setProvince(stateList);
    },
    handleTimezoneValue = (e) => {
      let updatedFormElement;
      //let updatedOrderForm;
      let value = e.target.value;
      if (value.length > 0) {
        const userValue = timeZoneList.filter((e) => e.label === value);
        updatedFormElement = {
          value: userValue[0].label,
          selectedIndex: userValue[0].value,
        };
      } else {
        updatedFormElement = {
          value: " ",
          selectedIndex: null,
        };
      }
      setBusinessData({
        ...businessData,
        timezoneValue: updatedFormElement.selectedIndex,
        timezoneLabel: updatedFormElement.value,
      });
    },
    submitForm = async (e) => {
      e.preventDefault();
      let logo = "";
      if (newLogo) {
        const formData = new FormData();
        formData.append("file", newLogo);
        formData.append("max_size", 100000);
        formData.append("allowed_ext", "image/png,image/jpg,image/jpeg");
        formData.append("directory", "Assets");
        const directory = "Assets";
        const res = await UploadFile(formData, directory);
        if (!res.success) return;
        const { url } = res.payload;
        logo = url;
      } else if (oldLogo) {
        // remove logo assets from here.
        const { success, message } = await DeleteFile(oldLogo);
        if (!success) {
          return setErrorMessage(message);
        }
      }

      const data = {
        ...businessData,
        logo:
          logo === ""
            ? businessData.logo === null
              ? null
              : businessData.logo
            : logo,
      };
      const res = await updateGeneralDetail(businessId, data);
      console.log("res", res);
      if (!res.success) {
        return setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      } else {
        setNewLogo(null);
        setSuccessMessage(res.message ?? "Settings saved.");
        // setTimeout(() => window.location.reload(), 1000);
      }
    },
    inputChangedFlag = (event) => {
      const newNumber = !event.includes("+") ? `+${event}` : event;
      setBusinessData({ ...businessData, number: newNumber });
    },
    setCoordinates = ({ lat, lng }) => {
      setBusinessData({
        ...businessData,
        latitude: lat,
        longitude: lng,
      });
    };

  const getBusinessInformationRef = async () => {
    const res = await getBusinessInformation(businessId);
    if (!res.success) {
      setErrorMessage(res.message);
    } else {
      setBusinessData({
        ...businessData,
        business_name: res.payload.business_name,
        logo: res.payload.logo === "" ? null : res.payload.logo,
        industry: res.payload.industry,
        website: res.payload.website,
        number: res.payload.mobile_number,
        address: res.payload.address,
        pin_code: res.payload.pin_code,
        country: res.payload.country,
        state: res.payload.state,
        city: res.payload.city,
        timezoneLabel: res.payload.timezone ? res.payload.timezone.label : "",
        timezoneValue: res.payload.timezone ? res.payload.timezone.value : "",
        latitude: res.payload.latitude,
        longitude: res.payload.longitude,
      });
      var country = res.payload.country;
      country &&
        fetch(`../../data/${country}.json`, {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        })
          .then((response) => response.json())
          .then((provinceData) => setProvince(provinceData));
      //setBusinessId(businessId);
    }
  };

  useEffect(() => {
    getBusinessInformationRef();
  }, []);

  return (
    <form
      onSubmit={(e) => submitForm(e)}
      className={classes["form-stacked-cards"]}
    >
      <div className="card-group">
        <h2 className="card-title">{"Business Details"}</h2>
        <div className="flex-row">
          <div className="grow-colum">
            <div className="field">
              <CustomTextFieldFilled
                type="text"
                placeholder="Business Name"
                rows={1}
                label="Business Name"
                name="business_name"
                value={business_name}
                required={true}
                id="business_name"
                onChange={(e) => inputChangedHandler(e)}
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                placeholder="Select Industry"
                label="Industry"
                variant="filled"
                type="select"
                value={industry !== "" ? industry : "Industry"}
                required={true}
                fullWidth
                name="industry"
                onChange={(e) => inputChangedHandler(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {industryList.map((industry, index) => {
                  return (
                    <MenuItem key={index} value={industry}>
                      {industry}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                type="text"
                name="website"
                value={website}
                onChange={(e) => inputChangedHandler(e)}
                id="website"
                placeholder="Website URL"
                label="Website URL"
              />
            </div>
            <div className="field">
              <PhoneInput
                placeholder="Phone number"
                value={number}
                fullWidth
                onChange={(phone) => inputChangedFlag(phone)}
                containerStyle={{ width: "100%" }}
              />
            </div>
          </div>
          <div className="shrink-colum">
            <FileAttachment
              name="file"
              value={
                newLogo
                  ? URL.createObjectURL(newLogo)
                  : logo && (logo !== null || logo !== "")
                  ? logo
                  : null
              }
              inputChangedHandler={(e) => inputChangedHandler(e)}
              removeAttachment={removeLogo}
              required={false}
              placeholder="Drag & Drop Profile Picture"
              width={"157px"}
              height={"157px"}
            />
          </div>
        </div>
      </div>
      <div className="card-group">
        <h2 className="card-title">{"Business Address"}</h2>
        <div className="flex-row">
          <div className="grow-colum">
            <div className="field">
              <CustomTextFieldFilled
                type="text"
                name="address"
                value={address}
                onChange={(e) => inputChangedHandler(e)}
                id="address"
                required={true}
                placeholder="Address"
                label="Address"
              />
            </div>
          </div>
        </div>
        <div className="flex-row min-gap">
          <div className="half">
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Country"
                placeholder="Country"
                // value={message.name || ""}
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                value={country !== "" ? country : "Country"}
                required={true}
                fullWidth
                name="country"
                onChange={(e) => HandleInputSelect(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {countriesList.map((country, index) => {
                  return (
                    <MenuItem key={index} value={country.name}>
                      {country.name}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
          </div>
          <div className="half">
            <div className="field">
              <CustomTextFieldFilled
                select
                placeholder="Select State"
                label="State"
                // value={message.name || ""}
                variant="filled"
                type="select"
                value={state !== "" ? state : ""}
                required={true}
                fullWidth
                name="state"
                onChange={(e) => inputChangedHandler(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {province &&
                  province.map((state, index) => {
                    return (
                      <MenuItem key={index} value={state.name}>
                        {state.name}
                      </MenuItem>
                    );
                  })}
              </CustomTextFieldFilled>
            </div>
          </div>
        </div>
        <div className="flex-row min-gap">
          <div className="half">
            <div className="field">
              <CustomTextFieldFilled
                type="text"
                name="city"
                value={city}
                onChange={(e) => inputChangedHandler(e)}
                required={true}
                id="City"
                placeholder="City"
                label="City"
              />
            </div>
          </div>
          <div className="half">
            <div className="field">
              <CustomTextFieldFilled
                type="number"
                name="pin_code"
                value={pin_code}
                onChange={(e) => inputChangedHandler(e)}
                required={true}
                id="pincode"
                placeholder="Pincode"
                label="Pincode"
              />
            </div>
          </div>
        </div>
      </div>
      <div className="card-group">
        <h2 className="card-title">{"Location Tagging & Timezone"}</h2>
        <div className="field">
          <Map
            forBusiness
            setCoordinates={setCoordinates}
            latlong={
              businessData.latitude && businessData.longitude
                ? { lat: businessData.latitude, lng: businessData.longitude }
                : null
            }
          />
        </div>
        <div className="field">
          <CustomTextFieldFilled
            id="Time-Zone"
            select
            label="Time Zone"
            placeholder="Select TimeZone"
            variant="filled"
            type="select"
            value={timezoneLabel !== "" ? timezoneLabel : ""}
            fullWidth
            name="timezone"
            required={true}
            //onChange={handleIndustryValue}
            onChange={(e) => handleTimezoneValue(e)}
            className={scssClasses["selectButtonWrapper"]}
          >
            {timeZoneList.map((timezone, index) => {
              return (
                <MenuItem key={index} value={timezone.label}>
                  {timezone.label}
                </MenuItem>
              );
            })}
          </CustomTextFieldFilled>
        </div>
      </div>
      <div className="action">
        <UIButton className="mt-4 ms-0">{"Save"}</UIButton>
      </div>
    </form>
  );
};

export default GeneralSettingsForm;
