import { useEffect, useRef, useState } from "react";
import { MenuItem } from "@material-ui/core";
import PhoneInput from "react-phone-input-2";

import { Input } from "../../../Components/exports";
import { UIButton } from "../../../Components/exports";
import FileAttachment from "../../../Components/UI/FileAttachment";
import leftArrow from "../../../assets/icons/leftArrow.svg";
import countriesList from "../../../data/countries.json";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useBusinessProfile } from "../../../contexts/BusinessProfileContext";
import {
  getBusinessInformation,
  updateGeneralDetail,
} from "../../../api/Business";
import { DeleteFile, UploadFile } from "../../../api/ChatFiles";
import { CustomTextFieldFilled } from "../../../Components/UI/CustomTextField/CustomTextField";
import { industryList, timeZoneList } from "../../../utils/GeneralSettingsData";

import Map from "./Map";

import scssClasses from "./MobileGeneralSettings.module.scss";

import useStyles from "./styles/muiStyles";

const MobileGeneralSettings = (props) => {
  const classes = useStyles(),
    { setSuccessMessage, setErrorMessage } = useToastMessage(),
    { setBusinessId } = useBusinessProfile(),
    [newLogo, setNewLogo] = useState(null),
    [oldLogo, setOldLogo] = useState(null),
    [businessData, setBusinessData] = useState({
      business_name: "",
      logo: "",
      industry: "",
      website: "",
      number: "",
      address: "",
      pin_code: "",
      country: "",
      state: "",
      city: "",
      timezoneLabel: "",
      timezoneValue: "",
      latitude: "",
      longitude: "",
    }),
    {
      business_name,
      logo,
      industry,
      website,
      number,
      address,
      pin_code,
      country,
      state,
      city,
      timezoneLabel,
    } = businessData,
    getBusinessInformationRef = useRef(() => {}),
    [province, setProvince] = useState([]),
    businessId = localStorage.getItem("businessId"),
    //const [generalDetails, setGeneralDetails] = useState("");
    // const [addressData, setAddressData] = useState("");
    //const [taggingTimezone, setTaggingTimezone] = useState({});
    removeLogo = () => {
      setOldLogo(businessData.logo);
      setNewLogo(null);
      setBusinessData({ ...businessData, logo: null });
    },
    inputChangedHandler = (e) => {
      let name = e.target.name;
      let value;
      if (e.target.type === "file") {
        const newLogo = e.target.files[0];
        setNewLogo(newLogo);
      } else {
        value = e.target.value;
        setBusinessData({ ...businessData, [name]: value });
      }
    },
    HandleInputSelect = async (event) => {
      const value = event.target.value;
      const name = event.target.name;
      setBusinessData({ ...businessData, [name]: value });
      const res = await fetch(`../data/${value}.json`, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const stateList = await res.json();
      setProvince(stateList);
    },
    handleTimezoneValue = (e) => {
      let updatedFormElement;
      //let updatedOrderForm;
      let value = e.target.value;
      if (value.length > 0) {
        const userValue = timeZoneList.filter((e) => e.label === value);
        updatedFormElement = {
          value: userValue[0].label,
          selectedIndex: userValue[0].value,
        };
      } else {
        updatedFormElement = {
          value: " ",
          selectedIndex: null,
        };
      }
      setBusinessData({
        ...businessData,
        timezoneValue: updatedFormElement.selectedIndex,
        timezoneLabel: updatedFormElement.value,
      });
    },
    submitForm = async (e) => {
      e.preventDefault();
      let logo = "";
      if (newLogo) {
        const formData = new FormData();
        formData.append("file", newLogo);
        formData.append("max_size", 10000);
        formData.append("allowed_ext", "image/png,image/jpg,image/jpeg");
        const directory = "Assets";
        const res = await UploadFile(formData, directory);
        if (!res.success) return;

        const { url } = res.payload;
        logo = url;
      } else if (oldLogo) {
        // remove logo assets from here.
        const { status } = await DeleteFile(oldLogo);
        if (status !== 200) return false;
      }

      const data = {
        ...businessData,
        logo:
          logo === ""
            ? businessData.logo === null
              ? null
              : businessData.logo
            : logo,
      };
      const res = await updateGeneralDetail(businessId, data);
      if (!res.success) {
        return setErrorMessage(res.message);
      } else {
        setNewLogo(null);
        setSuccessMessage(res.message ?? "Settings saved.");
        setTimeout(() => window.location.reload(), 1000);
      }
    },
    inputChangedFlag = (event) => {
      // let updatedFormElement;
      setBusinessData({ ...businessData, number: event });
    },
    setCoordinates = ({ lat, lng }) => {
      setBusinessData({
        ...businessData,
        latitude: lat,
        longitude: lng,
      });
    };

  getBusinessInformationRef.current = async () => {
    try {
      const res = await getBusinessInformation(businessId);
      console.log(
        "🚀 ~ file: MobileGeneralSettings.js ~ line 171 ~ getBusinessInformationRef.current= ~ res",
        res
      );
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        setBusinessData({
          ...businessData,
          business_name: res.payload.business_name,
          logo: res.payload.logo === "null" ? null : res.payload.logo,
          industry: res.payload.industry,
          website: res.payload.website,
          number: res.payload.mobile_number,
          address: res.payload.address,
          pin_code: res.payload.pin_code,
          country: res.payload.country,
          state: res.payload.state,
          city: res.payload.city,
          timezoneLabel: res.payload.timezone ? res.payload.timezone.label : "",
          timezoneValue: res.payload.timezone ? res.payload.timezone.value : "",
          latitude: res.payload.latitude,
          longitude: res.payload.longitude,
        });
        var country = res.payload.country;
        country &&
          fetch(`../../data/${country}.json`, {
            headers: {
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          })
            .then((response) => response.json())
            .then((provinceData) => setProvince(provinceData));
      }
    } catch (err) {
      setErrorMessage(err.message);
    } finally {
      setBusinessId(businessId);
    }
  };

  useEffect(() => {
    getBusinessInformationRef.current();
  }, []);

  return (
    <div className={classes["mobile-settings"]}>
      <div className="header">
        <a href="/business-settings">
          <img alt="back" src={leftArrow} style={{ margin: "0 10px" }} />
        </a>
        <h1 className="title">{"General Settings"}</h1>
      </div>
      <div className="content">
        <form onSubmit={(e) => submitForm(e)}>
          <div className="section">
            <h2 className="title">{"Business Details"}</h2>
            <div className="field">
              <FileAttachment
                name="file"
                value={
                  newLogo
                    ? URL.createObjectURL(newLogo)
                    : logo && (logo !== null || logo !== "")
                    ? logo
                    : null
                }
                inputChangedHandler={inputChangedHandler}
                removeAttachment={removeLogo}
                required={false}
                placeholder="Drag & Drop Profile Picture"
                width={"157px"}
                height={"157px"}
              />
            </div>
            <div className="field">
              <Input
                type="text"
                name="business_name"
                value={business_name}
                inputChangedHandler={(e) => inputChangedHandler(e)}
                required={true}
                id="business_name"
                placeholder="Business Name"
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                id="filled-select-currency"
                select
                placeholder="Select Industry"
                label="Industry"
                variant="filled"
                type="select"
                value={industry !== "" ? industry : "Industry"}
                required={true}
                renderValue={
                  industry !== ""
                    ? undefined
                    : () => (
                        <span className={scssClasses.placeHolderText}>
                          Industry
                        </span>
                      )
                }
                fullWidth
                name="industry"
                //onChange={handleIndustryValue}
                onChange={(e) => inputChangedHandler(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {industryList.map((industry, index) => {
                  return (
                    <MenuItem key={index} value={industry}>
                      {industry}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <Input
                type="text"
                name="website"
                value={website}
                inputChangedHandler={(e) => inputChangedHandler(e)}
                id="website"
                placeholder="Website URL"
              />
            </div>
            <div className="field">
              <PhoneInput
                placeholder="Phone number"
                value={number}
                fullWidth
                onChange={(phone) => inputChangedFlag(phone)}
                containerStyle={{ width: "100%" }}
              />
            </div>
          </div>
          <div className="section">
            <h2 className="title">{"Business Address"}</h2>
            <div className="field">
              <Input
                type="text"
                name="address"
                value={address}
                onChange={(e) => inputChangedHandler(e)}
                id="address"
                required={true}
                placeholder="Address"
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                id="filled-select-currency"
                select
                label="Country"
                placeholder="Select Country"
                // value={message.name || ""}
                variant="filled"
                type="select"
                value={country !== "" ? country : "Country"}
                required={true}
                renderValue={
                  country !== ""
                    ? undefined
                    : () => (
                        <span className={scssClasses.placeHolderText}>
                          Country
                        </span>
                      )
                }
                fullWidth
                name="country"
                onChange={(e) => HandleInputSelect(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {countriesList.map((country, index) => {
                  return (
                    <MenuItem key={index} value={country.name}>
                      {country.name}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                id="filled-select-currency"
                select
                label="State"
                placeholder="Select State"
                // value={message.name || ""}
                variant="filled"
                type="select"
                value={state !== "" ? state : "State"}
                required={true}
                renderValue={
                  state !== ""
                    ? undefined
                    : () => (
                        <span className={scssClasses.placeHolderText}>
                          State
                        </span>
                      )
                }
                fullWidth
                name="state"
                onChange={(e) => inputChangedHandler(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {province &&
                  province.map((state, index) => {
                    return (
                      <MenuItem key={index} value={state.name}>
                        {state.name}
                      </MenuItem>
                    );
                  })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <Input
                type="text"
                name="city"
                value={city}
                inputChangedHandler={(e) => inputChangedHandler(e)}
                required={true}
                id="City"
                placeholder="City"
              />
            </div>
            <div className="field">
              <Input
                type="number"
                me="pin_code"
                value={pin_code}
                inputChangedHandler={(e) => inputChangedHandler(e)}
                required={true}
                id="pin_code"
                placeholder="Pincode"
              />
            </div>
          </div>
          <div className="section">
            <h2 className="title">{"Location Tagging & Timezone"}</h2>
            <div className="field">
              <Map
                forBusiness
                setCoordinates={setCoordinates}
                latlong={
                  businessData.latitude && businessData.longitude
                    ? {
                        lat: businessData.latitude,
                        lng: businessData.longitude,
                      }
                    : null
                }
              />
            </div>
            <div className="field">
              <CustomTextFieldFilled
                id="Time Zone"
                select
                label="Time Zone"
                placeholder="Select TimeZone"
                variant="filled"
                type="select"
                value={timezoneLabel !== "" ? timezoneLabel : "Time Zone"}
                required={true}
                renderValue={
                  timezoneLabel !== ""
                    ? undefined
                    : () => (
                        <span className={scssClasses.placeHolderText}>
                          Time Zone
                        </span>
                      )
                }
                fullWidth
                name="timezone"
                //onChange={handleIndustryValue}
                onChange={(e) => handleTimezoneValue(e)}
                className={scssClasses["selectButtonWrapper"]}
              >
                {timeZoneList.map((timezone, index) => {
                  return (
                    <MenuItem key={index} value={timezone.label}>
                      {timezone.label}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
          </div>
          <div className="action">
            <UIButton className="ms-0">{"Save"}</UIButton>
          </div>
        </form>
      </div>
    </div>
  );
};

export default MobileGeneralSettings;
