import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "mobile-settings": {
    overflow: "hidden",
    width: "100%",

    "& > .header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      gap: "20px",
      height: "70px",
      padding: "20px",
      width: "100%",

      "& > a": {
        alignItems: "center",
        display: "flex",
        height: "100%",
      },

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
      },
    },

    "& .content": {
      padding: "36px 30px 30px",

      "& .section": {
        display: "flex",
        flexDirection: "column",
        marginBottom: "40px",

        "& .title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "21px",
          marginBottom: "30px",
        },

        "& .attachment": {
          "& .title": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "20px",
          },
        },

        "& .field": {
          marginBottom: "30px",

          "& .react-tel-input": {
            "& .selected-flag": {
              paddingLeft: 0,
            },
          },

          "& .inputComponent": {
            paddingLeft: "12px",
          },

          "& .inputComponent:focus + .labelComponent, & .inputComponent:valid + .labelComponent, & .inputComponent:read-only + .labelComponent":
            {
              left: "calc(14px - 1.5%)",
            },

          "& #googleMaps": {
            "& .map-wrapper": {
              "& > div": {
                width: "unset !important",

                "& > div": {
                  maxWidth: "100% !important",
                },
              },
            },

            "& div[aria-roledescription='map']": {
              cursor: "crosshair !important",
            },
          },
        },
      },

      "& .action": {
        display: "flex",
        justifyContent: "end",
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "form-stacked-cards": {
      display: "flex",
      flexDirection: "column",
      paddingBottom: "50px",
      paddingTop: "50px",
      maxWidth: "650px",
      rowGap: "36px",
      width: "100%",

      "& .card-group": {
        backgroundColor: "#ffffff",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        padding: "30px 36px 40px",

        "& .card-title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "21px",
          marginBottom: "30px",
        },

        "& .flex-row": {
          display: "flex",
          flexDirection: "column-reverse!important",
          gap: "30px 35px",

          "&.min-gap": {
            columnGap: "18px",
            rowGap: "0px",
          },

          /* "& .half": {
            flex: "0 0 auto",
            width: "calc((100% - 18px)/2)",
          },

          "& .grow-colum": {
            flexGrow: 1,
          },

          "& .shrink-colum": {
            flexShrink: 1,
          }, */
        },

        "& .field": {
          marginBottom: "30px",

          "& #googleMaps": {
            "& .map-wrapper": {
              "& > div": {
                width: "unset !important",

                "& > div": {
                  maxWidth: "100% !important",
                },
              },
            },

            "& div[aria-roledescription='map']": {
              cursor: "crosshair !important",
            },
          },

          "& .MuiFormControl-root": {
            width: "100%",
          },
        },
      },

      "& .action": {
        display: "flex",
        justifyContent: "end",
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "form-stacked-cards": {
      "& .card-group": {
        "& .flex-row": {
          flexDirection: "row!important",

          "& .half": {
            flex: "0 0 auto",
            width: "calc((100% - 18px)/2)",
          },

          "& .grow-colum": {
            flexGrow: 1,
          },

          "& .shrink-colum": {
            flexShrink: 1,
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
